footer {
  background-color: #100b20;
  text-align: center;
  padding: 7rem 10rem 2rem 10rem;
}

.content {
  text-align: start;
  display: flex;
  gap: 8%;
  margin-bottom: 4rem;
}

.content1 {
  width: 50%;
}

.content1 .intro {
  margin-top: 0.5rem;
  margin-bottom: 4rem;
  font-size: 0.75rem;
  line-height: 1.293rem;
  width: 90%;
}

.terms {
  font-size: 0.75rem;
  padding: 0.05rem 1rem;
}

.terms.of-use {
  border-right: 0.025rem solid var(--pink);
  padding-left: 0;
}

.content2 {
  /* border: 1px solid red; */
  width: 25%;
}

.useful-links {
  font-size: 0.875rem;
  color: var(--pink);
}

.content2 li {
  list-style: none;
  font-size: 0.75rem;
  margin: 1rem 0;
}

.socials {
  display: flex;
  align-items: center;
  margin-top: 2rem;
}

.follow {
  font-size: 0.75rem;
  margin-right: 0.5rem;
  color: var(--pink);
}

.socials .img {
  width: 1.5rem;
  height: 1.5rem;
  margin: 0 0.5rem;
}

.content3 {
  width: 18%;
}

.content3 .info {
  margin: 1rem 0;
}

.content3 .info .detail {
  font-size: 0.75rem;
  line-height: 1.18425rem;
}

.content3 .info .img {
  width: 0.75rem;
  /* height: 0.75rem; */
}

.logo {
  font-family: var(--header-font);
  font-size: 1.875rem;
}

.logo-dark {
  color: var(--pink);
}

small {
  font-size: 0.75rem;
}
